  }

  /* Summary na mobilu */
  .summary-section {
    padding: 1rem;
  }

  .summary-line {
    font-size: 0.95rem;
  }

  .summary-line.total {
    font-size: 1.2rem;
  }

  /* Ceník položky - větší texty */
  .item-name {
    font-size: 1rem;
  }

  .item-price {
    font-size: 1.05rem;
  }

  .item-description {
    font-size: 0.85rem;
    line-height: 1.5;
  }
}

