  }

  #total-parts {
    font-size: 1.3rem;
  }

  /* Calc result box - VĚTŠÍ */
  .result-box p {
    font-size: 0.95rem;
    margin: 0.6rem 0;
  }

  .highlight-price {
    font-size: 1.2rem;
  }
}

/* ========================================================================
   TOUCH FEEDBACK - Active states pro mobilní zařízení
   ======================================================================== */

@media (hover: none) and (pointer: coarse) {
  .radio-card:active,
  .checkbox-card:active,
  .btn-counter:active,
  .btn-primary:active,
  .btn-secondary:active,
  .suggestion-item:active {
    transform: scale(0.98);
    opacity: 0.8;
    transition: all 0.1s ease;
  }
}

/* ========================================================================
   CALCULATOR MOBILE OVERFLOW FIX - Oprava přetékání cen na mobilu
   ======================================================================== */

/* Kontejner kalkulačky - zabránit přetékání */
#kalkulacka,
.calculator-section,
.wizard-step,
.summary-section,
#summary-details {
  overflow: hidden;
  max-width: 100%;
  box-sizing: border-box;
}

/* Summary line - lepší responzivita */
@media (max-width: 480px) {
  .summary-line {
    grid-template-columns: 1fr auto;
    gap: 0.5rem;
    padding: 0.5rem 0;
    overflow: hidden;
    word-break: break-word;
  }

  .summary-line > span:first-child {
    min-width: 0;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .summary-price,
  .total-price {
    white-space: nowrap;
    flex-shrink: 0;
    min-width: fit-content;
    font-size: 0.9rem;
  }

  .summary-line.total .summary-price,
  .summary-line.total .total-price,
  #grand-total {
    font-size: 1.1rem;
  }

  .summary-subline {
    font-size: 0.7rem;
    padding-left: 0.5rem;
    word-break: break-word;
  }

  /* Souhrn - kompaktnější na mobilu */
  .summary-section {
    padding: 0.75rem;
  }

  .summary-section h3 {
    font-size: 1rem;
  }
}

/* Velmi malé obrazovky - stack layout */
@media (max-width: 360px) {
  .summary-line {
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
    align-items: flex-start;
  }

  .summary-line > span:first-child {
    width: 100%;
    font-size: 0.85rem;
  }

  .summary-price,
  .total-price {
    align-self: flex-end;
    font-size: 0.95rem;
  }

  .summary-line.total {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .summary-line.total .summary-price,
  #grand-total strong {
    font-size: 1.2rem;
  }
}

/* ========================================================================
   BUTTON TEXT FIT - Oprava textu v tlačítkách na mobilu
   ======================================================================== */

/* Základní pravidlo - text v tlačítkách se může zalamovat */
.btn-primary,
.btn-secondary,
.btn-danger,
.btn-admin,
.btn-reset {
  white-space: normal;
  word-wrap: break-word;
  overflow-wrap: break-word;
  text-align: center;
  line-height: 1.3;
}

/* Radio a checkbox karty - flexibilní výška pro text */
@media (max-width: 480px) {
  .radio-card,
  .checkbox-card {
    height: auto !important;
    min-height: 60px !important;
    max-height: none !important;
    overflow: visible !important;
    padding: 0.6rem 4rem 0.6rem 2.5rem !important;
  }

  .radio-title,
  .checkbox-title {
    font-size: 0.85rem !important;
    line-height: 1.25 !important;
    word-wrap: break-word !important;
  }

  .radio-desc,
  .checkbox-desc {
    font-size: 0.7rem !important;
    line-height: 1.3 !important;
    word-wrap: break-word !important;
  }

  .radio-price,
  .checkbox-price {
    font-size: 0.8rem !important;
    max-width: 3.5rem !important;
    text-align: right !important;
  }

  /* Wizard tlačítka - plná šířka s flexibilním textem */
  .wizard-buttons {
    gap: 0.5rem !important;
  }

  .wizard-buttons .btn-primary,
  .wizard-buttons .btn-secondary {
    padding: 0.75rem 1rem !important;
    font-size: 0.9rem !important;
    min-height: 48px !important;
    height: auto !important;
  }
}

/* Velmi malé obrazovky - zmenšení fontů */
@media (max-width: 360px) {
  .radio-card,
  .checkbox-card {
    padding: 0.5rem 3.5rem 0.5rem 2.25rem !important;
    min-height: 55px !important;
  }

  .radio-title,
  .checkbox-title {
    font-size: 0.8rem !important;
  }

  .radio-desc,
  .checkbox-desc {
    font-size: 0.65rem !important;
  }

  .radio-price,
  .checkbox-price {
    font-size: 0.75rem !important;
    right: 0.5rem !important;
  }

  .wizard-buttons .btn-primary,
  .wizard-buttons .btn-secondary {
    padding: 0.6rem 0.75rem !important;
    font-size: 0.85rem !important;
    min-height: 44px !important;
  }

  /* Counter tlačítka - menší na velmi malých displejích */
  .btn-counter {
    width: 40px !important;
    height: 40px !important;
    font-size: 1.1rem !important;
  }

  .counter-controls input[type="number"] {
    width: 55px !important;
    height: 38px !important;
    font-size: 1rem !important;
  }
}
